import {Component} from '@angular/core';
import {ROUTER_DIRECTIVES} from  '@angular/router';

import {MenuType} from  './menubar.Type';
import {MENU,RIGHTMENU} from  './menubar.datas';

@Component({
    selector: 'menu-bar-down',
  moduleId: module.id,
    templateUrl: 'menuBarDown.html',

    styleUrls: ["menuBarDown.css"],
    directives: [ROUTER_DIRECTIVES]
})
export class MenuBarDownComponent {
    rnd:number;
    menus:Array<MenuType>;
    rightMenus:Array<MenuType>;

    constructor() {
        this.menus = MENU;
        this.rightMenus = RIGHTMENU;
    }
    getRndNumber(){
      return Math.floor(Math.random() * 599996) + 1;
    }
    menuClose(){
        this.menus.forEach((obj)=> {
                obj.visible=false;
        });
    }
    menuOpen(menu) {
        this.menus.forEach((obj)=> {
            if (obj!=menu)
             obj.visible=false;
        });
        menu.visible = !menu.visible;
    }


}
